.container {
    display: flex;
    flex-direction: column;
    text-align: center;
    /* margin-top: 5dvh; */
    font-family: 'Courier New', Courier, monospace;
    /* height: 100dvh; */

    p {
      font-size: 20px;
      color: var(--secondary-color);
      margin: 0;
      font-family: 'Courier New', Courier, monospace;
  }
  form {
    display: flex;
    flex-direction: column;
    gap: 30px;
}

textarea,
    input:not([type='submit']) {
    height: 50px;
    padding-left: 16px;
    width: 250px;
}   

textarea {
    padding-top: 16px;
    resize: none;
    height: 250px;
  }
  
  input,
  textarea {
    border-radius: 20px;
    border: #888 1px solid;
    box-sizing: border-box;
    justify-self: center;
    background-color: var(--background-color);
    color: white;
    height: 37dvh;
    font-family: 'Courier New', Courier, monospace;
  }

input[type='submit'] {
    background-color: var(--secondary-color);
    color: white;
    border: none;
    border-radius: 20px;
    width: 126px;
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    transition: box-shadow 200ms ease-in-out, transform 200ms ease-in-out;
    margin: auto;
    margin-bottom: 15px;
}
input[type='submit']:hover {
    transform: scale(1.05);
}

input[type='submit']:active {
    transform: translateY(2px);
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
}




@media (width >= 800px) {
textarea,
input:not([type='submit']) {
  width: 500px;
  font-size: 17px;
}
form {
  gap: 40px;
}
}

@media (width >= 1400px) {
textarea,
input:not([type='submit']) {
  width: 800px;
}
}

}
  
    